import httpUtils from 'config/index'
class commonService {
  /**
   * 获取广告接口
   * @param params
   * @returns {*}
   */
  getAdve(params) {
    return httpUtils.get("/index/index/image_data_list", params);
  }

  /**
   * 获取热门排行
   */
  getHot(){
    return httpUtils.get("/index/item/hot_item_list");
  }

  /**
   * 获取手机验证码
   */
  getPhoneCode(params){
    return httpUtils.get("/index/login/get_code",params);
  }
  
  /**
   * 获取图片验证码
   */
  getImgCode(){
    return httpUtils.get("/index/login/imageCode");
  }

  /**
   * 获取热门咨询
   */
  getHotConsult(){
    return httpUtils.get("/index/article/get_hot_article");
  }
}
export default new commonService()



