<template>
    <div class="industryList">
      <div class="title clearfix" >
        <span class="fl" >行业资讯</span>
        <router-link class="fr more"  to="/newsList?type=1"> 更多 <span class="el-icon-arrow-right"></span></router-link>
      </div>
      <div class="special">
        <router-link :to="'/consultDetail?id='+data.rArticle[0].id">
          <img :src="data.rArticle[0].img" alt="">
          <div>{{data.rArticle[0].title}}</div>
        </router-link>
      </div>
      <ul>
        <li v-for="item in data.aticle" :key="'industry'+item.id"><router-link :to="'/consultDetail?id='+item.id">{{item.title}}</router-link></li>
      </ul>
    </div>
</template>

<script>
  import api from "@/api/home-api"
  export default {
        name: "industryList",
        data(){
          return {
            data: {rArticle:[{img:'',title:''}]}
          }
        },
        mounted () {
          this.getData();
        },
        methods:{
          getData(){
            api.getIndustry().then( data => {
              this.data = data;
            });
          }
        }
    }
</script>

<style lang="less" scoped type="text/less">
  ul{
    width: 295px;
    padding:20px 10px 10px 40px;
    box-sizing: border-box;
 }
  li{
    list-style: disc;
    a{
      display: block;
      line-height: 28px;
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
      cursor: pointer;
    }
  }
  .industryList{
    width: 295px;
    height: 522px;
    background-color: #fff;
    .title{
      height: 50px;
      line-height: 50px;
      font-size: 14px;
      font-weight: bold;
      padding: 0 19px;
      border-bottom: 1px solid #E3E4E5;
      .more{
        color: #999;
        font-size: 12px;
        font-weight: normal;
        cursor: pointer;
      }
    }

    .special{
      cursor: pointer;
      img{
        width: 260px;
        height: 178px;
        margin:20px;

      }
      div{
        width: 220px;
        line-height: 40px;
        color: #fff;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
        padding: 0 20px;
        background-color: rgba(0,0,0,1);
        margin: -70px  20px 0 20px;
        z-index: 100;
      }
    }


  }
</style>



