<template>
    <div class="hotOrder">
      <div class="title clearfix" >
          <span class="fl" >热门排行</span>
          <router-link v-if="more" to="/list?order=1" class="fr more" > 更多 <span class="el-icon-arrow-right"></span></router-link>
      </div>
      <div class="list">
          <router-link :to="'/detail?id='+item.id" class="item clearfix" v-for="(item,index) in data" :key="'order'+item.id">
            <div class="order fl">{{index+1}}</div>
            <div class="name fl">{{item.brand}}</div>
            <div class="count fr">{{item.read_num}}</div>
          </router-link>
      </div>
    </div>
</template>

<script>
  import api from "@/api/home-api"
    export default {
        name: "hotOrder",
        props:{
          more: {
            type: Boolean,
            default: true, //是否显示更多按钮
          }
        },
        data(){
            return {
              data: ''
            }
        },
        mounted () {
          this.getData();
        },
        methods:{
          getData(){
            api.getHotOrder().then( data => {
               this.data = data;
            });
          }
        }
    }
</script>

<style lang="less" scoped type="text/less">
  .hotOrder{
    height: 490px;
    background-color: #fff;
    .title{
      height: 50px;
      line-height: 50px;
      font-size: 14px;
      font-weight: bold;
      padding: 0 19px;
      border-bottom: 1px solid #E3E4E5;
      .more{
        color: #999;
        font-size: 12px;
        font-weight: normal;
        cursor: pointer;
      }
    }
  }
  .list{
    padding:13px 20px 23px 20px;
    .item{
      padding-top:10px;
      display: block;
      .order{
        width: 18px;
        height: 21px;
        text-align: center;
        line-height: 21px;
        font-size: 12px;
        color: #999;
        background: url("../../static/image/tag_order_default.png") 100% 100% no-repeat;
      }
      .name{
        width: 65%;
        font-size: 14px;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        margin-left: 10px;
      }
      .count{
        color: #999;
        font-size: 12px;
      }
    }
    .item:nth-child(-n+3) .order{
      color: #fff;
      background: url("../../static/image/tag_order.png") 100% 100% no-repeat;
    }
  }

</style>



