<template>
    <div class="search ">
      <div class="wrapper">
        <router-link to="/">
          <img src="../../static/image/logo.png" alt="" class="logo">
        </router-link>
        <div>
          <el-input placeholder="请输入内容" v-model="keyword" class="input-with-select" clearable>
            <i slot="prefix" class="el-input__icon el-icon-search"></i>

              <el-button slot="append" >
                <router-link :to="'/list?keyword='+keyword">搜索</router-link>
              </el-button>

          </el-input>
          <div class="tag">
            <span class="name">热门搜索：</span>
            <router-link to="'../pages/list/detail?id=31136">万邦教育</router-link>
            <router-link to="'../pages/list/detail?id=31136">蜀汉麻辣烫</router-link>
            <router-link to="'../pages/list/detail?id=31136">萌贝树母婴</router-link>
            <router-link to="'../pages/list/detail?id=31136">俏阿瑶米线</router-link>
            <router-link to="'../pages/list/detail?id=31136">朝茶店</router-link>
          </div>
        </div>
        <router-link to="/evaluate">
          <img src="../../static/image/evaluate.png" alt="" class="evaluate">
        </router-link>
      </div>

    </div>
</template>

<script>
    export default {
        name: "search",

      data(){
        return {
          keyword:''
        }
      },
      watch:{
        $route(to,from){
          console.log("ro.path=========",to.query);
         this.keyword = to.query.keyword;
        }
      },
    }
</script>

<style scoped>
    .search{
      background-color: #fff;

    }
    .search .wrapper{
      display: flex;
      justify-content: space-between;
      align-items: center;
      height: 128px;

    }
    .logo{
      width: 194px;
      height: 68px;
    }
    .evaluate{
      width: 200px;
      height: 59px;

    }
    .el-input {
      width: 430px;
    }
    .tag{
      font-size: 12px;
      color: #808080;
      text-align: start;
      margin-top: 10px;
    }
    .tag span{
      margin-right: 20px;
      cursor: pointer;
    }
    .tag .name{
      color: #1a1a1a;
      font-weight: bold;
      margin-right: 0;
      cursor: default;
    }
</style>



