    .header {
      display: block;
      font-size: 16px;
      line-height: 60px;
      padding: 31px 21px;
      border-bottom: 1px solid #E3E4E5;

      .avatar {
        width: 60px;
        height: 60px;
        margin-right: 20px;
        vertical-align: middle;
        border-radius: 50%;
