/**
 * 引入fetch、baseUrl
 * @param params
 * @returns {*}
 */
import httpUtils from 'config/index'

class homeService {


  getData() {
    return httpUtils.get(`/index/index/index_home`)
  }


  getCate(){
    return httpUtils.get("/index/category/get_cate_list");
  }

  /**
   * 获取热门排行
   * @returns {*}
   */
  getHotOrder(){
    return httpUtils.get("/index/item/hot_item_list");
  }
  getIndustry(){
    return httpUtils.get("/index/article/get_article_new");
  }
  getStrategy(){
    return httpUtils.get("/index/article/strategy");
  }

}
export default new homeService()



